@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;900&display=swap);
.group-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
    position: relative;
}

.group-container::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 70%;
    height: 2px;
    background-color: rgb(228, 228, 228);
}

table{
    padding: 10px 30px;
    /* background-color: rgb(255, 228, 196); */
}

td,th{
    text-align: left;
}

th{
    color: rgb(206, 0, 51);
    padding: 0 10px 10px;

}

td{
    padding: 3px 0;
}

.td-country{
    width: 230px;
}

.td-points{
    width: 50px;
    padding: 0 10px;
}

.group-matches{
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

@media all and (max-width: 1000px){
    .group-container{
        flex-direction: column;
    }

    table{
        margin-bottom: 25px;
        padding: 0 30px 10px;
    }

    th{
        font-size: 14px;
        padding: 0 2px 10px;
    }

    .td-country{
        width: 180px;
    }
}
.match-list{
    display: flex;
    position: relative;
    white-space: nowrap;
    margin-top: 2px;
}

input{
    width: 25px;
    text-align: center;
    padding: 2px;
}


.team1,
.team2{
    position: absolute;
    font-size: 20px;
}

.team1{
    right: 105%;
}

.team2{
    left: 105%;
}

img{
    border: 1px solid black;
    height: 15px;
    margin: 0 5px;
}

@media all and (max-width: 655px){
    .team1,
    .team2{
        font-size: 15px;
    }

    img{
        height: 12px;
    }
}


.ros-match{
    display: flex;
    position: relative;
    white-space: nowrap;
    margin-top: 2px;
}

input{
    width: 25px;
    text-align: center;
    padding: 2px;
}

.ros-team1,
.ros-team2{
    position: absolute;
    font-size: 20px;
}

.ros-team1{
    right: 105%;
}

.ros-team2{
    left: 105%;
}

@media all and (max-width: 655px){
    .ros-team1,
    .ros-team2{
        font-size: 15px;
    }
}
.tree-container{
    width: 100%;
    padding: 50px 0;
    background-image: url(/WorldCupSimulator/static/media/stadium.5662577c.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.tree-container h2{
    text-align: center;
    color:rgb(206, 0, 51);
    padding-bottom: 20px;
}

.finals-box{
    padding: 30px;
    margin: 30px;
    width: 75%;
    background-color: rgb(255, 228, 196);
}

.final{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.matches{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.penalty{
    width: 17px;
    height: 17px;
    margin: 2px;
}

.penalty-hidden{
    width: 17px;
    height: 17px;
    margin: 2px;
    pointer-events:none;
    opacity: .3;
}

.untargetable{
    opacity: .3;
    pointer-events: none;
}

.winner{
    margin: 25px;
    padding: 15px;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.winner img{
    height: 30px;
    margin: 5px;
}

@media all and (max-width: 980px){
    .matches{
        flex-direction: column;
    }
}

@media all and (max-width: 450px){
    .finals-box{
        width: 100%;
    }
}

.container{
    width: 100%;
}

h1{
    text-align: center;
    background-color: rgb(206, 0, 51);
    color: rgb(255, 228, 196);
    padding: 10px;
    letter-spacing: 2px;
    position: relative;
}

.gen-btn-container{
    width: 100%;
    padding: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}



